body {
  padding: 0;
  margin: 0;
  background-color: aliceblue;
}
a {
  text-decoration: none;
}
.search {
  width: auto;
  position: relative;
  top: auto;
  left:100%;
  height: 40px;
  border-radius: 40px;
  padding: 10px;
  transform: translate(-50%, -50%);
  background: #2f3640;
  color: #fff;
  font-size: 16px;
  transition: 0.6s;
  line-height: 40px;
  
}
.btn {
  border-radius: 50%;
  background: #2f3640;
  color: #2471a3;
  float: right;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.text {
  border: none;
  background: none;
  outline: none;
  float: left;
  padding: 0;
  color: #fff;
  font-size: 16px;
  transition: 0.6s;
  line-height: 40px;
  width: 0px;
}

.Heading {
  position: relative;
  color: #2f3640;
  font-size: 400%;
  font-family: "Rubik Moonrocks", cursive;
}
